@import url(https://fonts.googleapis.com/css2?family=Josefin+Sans&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body{background:#2F384B;font-family:"Josefin Sans"}#root{display:flex;justify-content:center;align-items:center;flex-wrap:wrap;margin:5px;transition:all 0.2s ease-in-out;height:100vh}.box{display:flex;justify-content:center;align-items:center;flex-wrap:wrap;margin:5px;transition:all 0.2s ease-in-out;padding:20px;border-radius:5px;box-shadow:inset 0 0 1px 1px #8e9bb8;background:#1b212c;color:#FAFAFA;flex-direction:column;flex-basis:400px}.incControls{display:flex}.incControls *{margin:auto}.headText{display:flex;justify-content:center;align-items:center;flex-wrap:wrap;margin:5px;transition:all 0.2s ease-in-out;flex-direction:column}.display{display:flex;justify-content:center;align-items:center;flex-wrap:wrap;margin:5px;transition:all 0.2s ease-in-out;font-family:"Josefin Sans",sans-serif;width:200px;height:200px;border-radius:50%;font-size:2.4em}.controls{display:flex;justify-content:center;align-items:center;flex-wrap:wrap;margin:5px;transition:all 0.2s ease-in-out}.controls div>.btn{font-size:0.8em;margin:5px;width:2.5rem;height:2.5rem;border-radius:50%;border:2px solid #09E698}.controls div>.btn:hover{background:#09E698;color:#FAFAFA}.btn{cursor:pointer;margin:5px;border:none;outline:none;background:transparent;color:#8e9bb8}.btn:hover{color:#cdd3df}.btnBoxGroup{display:flex;justify-content:center;align-items:center;flex-wrap:wrap;margin:5px;transition:all 0.2s ease-in-out;transition:all 1s ease-in-out}.btnBox{margin:10px}

